import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import rightBox from '@/components/common-components/right-box.vue';
import vueQr from 'vue-qr';
let noticeConfig = class noticeConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '';
        this.agentList = [];
        this.showmore = false;
        this.oprateConfig = [{
                text: '',
                action: 'add'
            }];
        this.showQr = false;
        this.downloadData = {
            url: ''
        };
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    mounted() {
        this.getAgentList(1);
    }
    getAgentList(page) {
        let queryPage = page || 1;
        this.$store.dispatch('getAgentList', {
            schoolId: this.schoolInfo.id,
            page: queryPage
        }).then((e) => {
            if (e.success) {
                if (e.data.length < 10) {
                    this.showmore = false;
                }
                if (page === 1) {
                    this.agentList = e.data;
                }
                else {
                    this.agentList = this.agentList.concat(e.data);
                }
            }
        });
    }
    boxClick(action) {
        if (action === 'add') {
            const component = this.$refs['myPopup'];
            component.show();
            let time = new Date().getTime();
            this.downloadData.url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx76ce6e843b515382&redirect_uri=https://www.memeschool.top/mt/inviteadmin&response_type=code&scope=snsapi_base&state=' + this.schoolInfo.id + '@' + time + '@agent#wechat_redirect';
            this.showQr = true;
        }
    }
    maskClick() {
        const component = this.$refs['myPopup'];
        component.hide();
        this.showQr = false;
    }
    refresh() {
        this.maskClick();
        this.getAgentList(1);
    }
    phoneClick(phone) {
        window.location.href = 'tel://' + phone;
    }
};
noticeConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            rightBox,
            vueQr
        }
    })
], noticeConfig);
export default noticeConfig;
